.contacts-popup {
  position: absolute;
  margin: 1.5em;
  bottom: 0;
  right: 0;

  .contacts-link {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px auto 0;
    cursor: pointer;
    -webkit-transition: all 0.1s ease-out;
    transition: all 0.1s ease-out;
    position: relative;

    &.contacts-button{
      background-color: transparent;
      border: none;
      outline: none;
      font-size: 1em;
    }

    &:hover {
      transform: scale(1.2);
    }

    &.contacts-popup__link {

      i{
        color: $white;
      }

      &:not(:last-child) {
        margin: 20px auto 0;
      }

      &:nth-last-child(1) {
        -webkit-transition-delay: 25ms;
        transition-delay: 25ms;
      }
      &:not(:last-child):nth-last-child(2) {
        -webkit-transition-delay: 20ms;
        transition-delay: 20ms;
      }
      &:not(:last-child):nth-last-child(3) {
        -webkit-transition-delay: 40ms;
        transition-delay: 40ms;
      }
      &:not(:last-child):nth-last-child(4) {
        -webkit-transition-delay: 60ms;
        transition-delay: 60ms;
      }
      &:not(:last-child):nth-last-child(5) {
        -webkit-transition-delay: 80ms;
        transition-delay: 80ms;
      }
      &:not(:last-child):nth-last-child(6) {
        -webkit-transition-delay: 100ms;
        transition-delay: 100ms;
      }

      &.hidden {
        display: none;
      }
    }

    
  }
}

[tooltip] {
  &:before {
    font-weight: 600;
    border-radius: 2px;
    background-color: #3d4a57;
    color: #fff;
    border-radius: 0.5em;
    content: attr(tooltip);
    font-size: 0.3em;
    visibility: hidden;
    opacity: 0;
    padding: 9px 12px;
    margin-left: 10px;
    position: absolute;
    right: 100%;
    bottom: 47%;
    transform: translateY(50%);
    white-space: nowrap;
  }

  &:hover:before,
  &:hover:after {
    visibility: visible;
    opacity: 1;
  }
}

@media (max-width: 800px) {
  [tooltip]:not(:last-child):before,
  [tooltip]:not(:last-child):after {
    visibility: visible;
    opacity: 1;
  }
}
