.introduce-page {
  .cloud {
    position: relative;
    background-color: $white;
    padding: 10px 15px;
    margin: 0 10px 20px 10px;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:before {
      content: ' ';
      position: absolute;
      width: 0;
      height: 0;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 30px);
      border: 15px solid transparent;
      border-top: 15px solid $white;
    }

    .typedJS {
      color: $bg-gray;
      font-size: 0.7em;
      margin: 3px;
      font-family: 'IBM Plex Mono', monospace;

      a {
        color: $blue;

        &:hover {
          color: $link-highlighted;
          &::before {
            width: 100vw;
            transform: translateY(90px);
          }
        }

        &::before {
          content: '';
          pointer-events: none;
          background-image: url('../img/octocat.png');
          background-position: 0 30%;
          background-repeat: no-repeat;
          background-size: 200px;
          position: absolute;
          height: 90px;
          top: -90px;
          left: 0;
          transition: transform 0.3s;
        }

        &::after {
          content: '';
          pointer-events: none;
          background-color: $bg-gray;
          position: absolute;
          top: -100px;
          left: 0;
          width: 200px;
          height: 100px;
        }
      }
    }
  }

  .avatar {
    height: 20vh;
    pointer-events: none;
  }

  .projects-link {
    position: absolute;
    bottom: 2em;
    cursor: pointer;
    height: fit-content;
    display: flex;

    .icon-down-open {
      transition: 0.3s;
      font-size: 25px;
      animation: blinker 1.7s infinite ease-out;
      transition: 0.3s;
    }

    .icon-down-open:hover {
      transform: translateY(-3px) scale(1.15);
      text-shadow: 0 3px 5px $almost-black;
    }
  }

  @keyframes blinker {
    0% {
      opacity: 1;
    }
    50% {
      opacity: 0.5;
    }
    100% {
      opacity: 1;
    }
  }
}
