.contacts-page {
  .contacts {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    a {
      margin: 10px;
      padding: 10px;
      transition: 0.3s;
      color: $white;

      &:hover {
        background-color: $dark-blue;
        border-radius: 10px;
      }
    }
  }
}

@media screen and (max-width: 600px) {
  .contacts-page {
    .contacts {
      flex-direction: column;
    }
  }
}
