.project {
  font-size: 0.5em;
  padding: 15px;
  padding-bottom: 5px;
  background-color: $white;
  border-radius: 25px;
  width: fit-content;
  transition: 0.3s;
  outline: none;
  margin: 20px 10px;
  width: 300px;

  &:hover {
    transform: scale(1.01);
    box-shadow: 0px 2px 5px $almost-black;
  }

  .title {
    text-align: left;
    color: $gray-blue;
    margin-top: 0.3em;
    margin-bottom: 0.1em;
  }

  .feauters {
    background-color: $light-blue;
    padding: 0.2em;
    border-radius: 0.3em;
    margin-bottom: 0.5em;

    h3 {
      background-color: $blue;
      color: $white;
      border-radius: 0.3em;
      padding: 0.5em;
      margin: 0;
    }

    ul {
      margin: 0.5em;
      padding: 0;

      li {
        list-style-type: none;
        margin: 0.2em 0;
        text-align: left;
        color: $blue;

        &:last-child {
          margin: 0;
        }

        &::before {
          margin-right: 0.5em;
          content: '•';
          color: $blue;
        }
      }
    }
  }

  .instrument-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: $light-blue;
    border-radius: 0.3em;
    padding: 0.1em 0.2em;
    margin: 0.5em 0;

    &:last-child {
      margin: 0;
    }

    p {
      padding: 0.3em;
      height: fit-content;
      margin: auto 0;
      color: $blue;
    }

    .multiple-instruments {
      display: flex;
      flex-direction: column;
    }

    span {
      margin: 0.1em 0;
      background-color: $blue;
      border-radius: 0.3em;
      padding: 0.3em 0.5em;
      color: $white;
    }
  }

  .links {
    margin-top: 1em;
    display: flex;
    flex-direction: row;
    justify-content: center;

    a {
      padding: 0.8em;
      margin: 0.5em;
      background-color: $blue;
      color: $white;
      border-radius: 0.3em;
      text-decoration: none;
      transition: 0.3s;
      box-shadow: 0 -5px $dark-blue inset;

      &:hover,
      &:focus {
        background-color: $dark-blue;
        border-radius: 0.3em;
        text-decoration: none;
        box-shadow: none;
      }
    }
  }

  .icons {
    width: fit-content;
    margin: 3px 0 3px auto;

    img {
      display: inline-block;
      margin: 0 3px;
    }
  }
}
